import request from '../request'


// 获取列表
export function listDict(data) {
    return request({
        url: '/api/dict/listDict',
        method: 'post',
        data
    })
}

// 新增
export function saveDict(data) {
    return request({
        url: '/api/dict/saveDict',
        method: 'post',
        data
    })
}

// 修改
export function updateDict(data) {
    return request({
        url: '/api/dict/updateDict',
        method: 'post',
        data
    })
}

// 删除
export function deleteDictById(params) {
    return request({
        url: '/api/dict/deleteDictById',
        method: 'get',
        params: params
    });
}

// 根据id查询
export function getDictById(params) {
    return request({
        url: '/api/dict/getDictById',
        method: 'get',
        params: params
    });
}
