<template>
  <div id="style-operate">
    <!-- 弹出框 -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="1200" draggable>
      <!-- 主表 Form -->
      <el-form :inline="true" :model="form" :rules="rules" ref="ruleForm" class="demo-form-inline" :label-width="100" label-position="right">
        <el-form-item :label="$t('DictView.dictCode')" prop="dictCode" :label-width="formLabelWidth" >
          <el-input :readonly="isReadonly" v-model="form.dictCode" :placeholder="$t('DictView.dictCodePlaceholder')" autocomplete="off"/>
        </el-form-item>
        <el-form-item :label="$t('DictView.dictName')" prop="dictName" :label-width="formLabelWidth" >
          <el-input :readonly="isReadonly" v-model="form.dictName" :placeholder="$t('DictView.dictNamePlaceholder')" autocomplete="off"/>
        </el-form-item>
        <el-form-item :label="$t('DictView.nameEn')" prop="nameEn" :label-width="formLabelWidth" >
          <el-input :readonly="isReadonly" v-model="form.nameEn" :placeholder="$t('DictView.nameEnPlaceholder')" autocomplete="off"/>
        </el-form-item>
        <el-form-item :label="$t('DictView.dictRemark')" :label-width="formLabelWidth" >
          <el-input :readonly="isReadonly" v-model="form.dictRemark" :placeholder="$t('DictView.dictRemarkPlaceholder')" autocomplete="off"/>
        </el-form-item>
      </el-form>

      <!-- 子表新增 Form -->
      <el-divider content-position="right">{{ $t('DictView.divider') }}</el-divider>
      <el-button type="primary" @click="this.fromif = false" v-show="type != 'detail'" v-if="fromif" >
        {{ $t('common.button.add') }}
      </el-button>
      <div v-if="!fromif">
        <el-form :inline="true" :model="detail" class="demo-form-inline" :label-width="50" label-position="right">
          <el-form-item :label="$t('DictView.dictKey')" label-width="60px" style="margin-right: 20px;">
            <el-input v-model="detail.dictKey" :placeholder="$t('DictView.dictKeyPlaceholder')" autocomplete="off" style="width: 150px;"/>
          </el-form-item>
          <el-form-item :label="$t('DictView.keyEn')" label-width="60px" style="margin-right: 20px;">
          <el-input v-model="detail.keyEn" :placeholder="$t('DictView.keyEnPlaceholder')" autocomplete="off" style="width: 150px;"/>
        </el-form-item>
          <el-form-item :label="$t('DictView.dictValue')" label-width="60px" style="margin-right: 20px; margin-left: 0px">
            <el-input v-model="detail.dictValue" :placeholder="$t('DictView.dictValuePlaceholder')" autocomplete="off" style="width: 150px;"/>
          </el-form-item>
          <el-form-item :label="$t('DictView.dictDetailRemark')" label-width="60px" style="margin-right: 20px;">
            <el-input v-model="detail.dictDetailRemark" :placeholder="$t('DictView.dictDetailRemarkPlaceholder')" autocomplete="off" style="width: 180px;"/>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addDs()">
              {{ $t('common.button.select') }}
            </el-button>
            <el-button @click="this.fromif = true">
              {{ $t('common.button.cancel') }}
            </el-button>
          </el-form-item>
        </el-form>
      </div>

      <!-- 子表数据 List -->
      <el-table :data="form.detailList" stripe border fit highlight-current-row :empty-text="$t('common.table.noData')"
                style="width: 100%; margin-top: 5px;" max-height="300">
        <el-table-column fixed type="index" :label="$t('common.table.number')" width="90"/>
        <el-table-column prop="dictKey" :label="$t('DictView.dictKey')" width="180">
          <template v-slot="{ row, column, $index }">
            <el-input v-model="row.dictKey" :placeholder="$t('DictView.dictKeyPlaceholder')" v-if="row.ifinp" style="width: 98%;"/>
          </template>
        </el-table-column>
        <el-table-column prop="keyEn" :label="$t('DictView.keyEn')" width="180">
          <template v-slot="{ row, column, $index }">
            <el-input v-model="row.keyEn" :placeholder="$t('DictView.keyEnPlaceholder')" v-if="row.ifinp" style="width: 98%;"/>
          </template>
        </el-table-column>
        <el-table-column prop="dictValue" :label="$t('DictView.dictValue')" width="150">
          <template v-slot="{ row, column, $index }">
            <el-input v-model="row.dictValue" :placeholder="$t('DictView.dictValuePlaceholder')" v-if="row.ifinp" style="width: 98%;"/>
          </template>
        </el-table-column>
        <el-table-column prop="dictEnable" :label="$t('DictView.dictEnable')" width="180">
          <template v-slot="{ row, column, $index }">
            <el-switch v-model="row.dictEnable" :active-value="1" :inactive-value="0"
                       :active-text="$t('pullDown.enable.enable')" :inactive-text="$t('pullDown.enable.disable')"></el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="dictDetailRemark" :label="$t('DictView.dictDetailRemark')" >
          <template v-slot="{ row, column, $index }">
            <!-- 文本输入框 -->
            <el-input v-model="row.dictDetailRemark" :placeholder="$t('DictView.dictDetailRemarkPlaceholder')" v-if="row.ifinp" style="width: 98%;"/>
          </template>
        </el-table-column>
        <el-table-column fixed="right" :label="$t('common.table.operation')" width="200" v-if="type != 'detail'">
          <template #default="scope">
            <el-button type="success" size="small" @click="scope.row.ifinp = false" v-if="scope.row.ifinp">
              {{ $t('common.button.confirm') }}
            </el-button>
            <el-button type="warning" size="small" @click="scope.row.ifinp = true" v-if="!scope.row.ifinp">
              {{ $t('common.button.edit') }}
            </el-button>
            <el-button type="danger" size="small" @click="deleteRow(scope.$index, scope.row)">
              {{ $t('common.button.delete') }}
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <template #footer>
      <span class="dialog-footer">
        <el-button @click="closeButton()">
          {{ $t('common.button.cancel') }}
        </el-button>
        <el-button type="primary" v-show="type != 'detail'" @click="submitButton()">
          {{ $t('common.button.confirm') }}
        </el-button>
      </span>
      </template>
    </el-dialog>


  </div>
</template>

<script>
import {ElNotification} from "element-plus";
import {getDictById, saveDict, updateDict} from "@/http/basics/dictApi";

export default {
  name: "style-operate",
  components: {},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 控制对话框大开闭
      dialogFormVisible: false,
      // 联动列表数据
      rowData: {},
      // 行数据副本
      form: {},
      // 表单验证规则
      rules: {
        dictCode: [
          { required: true, message: this.$t('DictView.dictCodePlaceholder'), trigger: 'blur' },
        ],
        dictName: [
          { required: true, message: this.$t('DictView.dictNamePlaceholder'), trigger: 'blur' },
        ],
        nameEn: [
          { required: true, message: this.$t('DictView.nameEnPlaceholder'), trigger: 'blur' },
        ]
      },
      // 进入类型: 新增 详情 编辑
      type: '',
      titleName: this.$t('common.dialog.defaultTitle'),
      // 表单的只读
      isReadonly: false,
      // 表单前面的label宽度
      formLabelWidth: '100px',

      // 下拉的数据集合对象
      selectData: { },

      // 添加子表列表控制
      fromif: true,
      detail: {},
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    type(newVal, oldVal) {
      if (newVal == '') return
      if (newVal == 'add') this.titleName = this.$t('common.dialog.addTitle')
      if (newVal == 'edit') this.titleName = this.$t('common.dialog.editTitle')
      if (newVal == 'detail') this.titleName = this.$t('common.dialog.detailTitle')
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    // this.getPullDownData();
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getPullDownData() {

    },
    // 父组件调用 进行初始化
    getRow(id) {
      getDictById({
        id: id
      }).then(res => {
        if (res.data.status == 200) this.form = res.data.data;
      }).catch((erro) => {
      });
    },

    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible = false;
      this.isReadonly = false;
      this.form = {};
      this.rowData = {};
      this.$refs.ruleForm.resetFields(); // 重置表单验证
    },
    // 确认按钮
    submitButton() {
      // 提交校验
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          if (this.type == "add") {
            saveDict(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible = false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => {});
          } else if (this.type == "edit") {
            updateDict(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible = false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => {});
          }
          this.closeButton();
        } else {
          return false;
        }
      });
    },

    // 列表新增
    addDs() {
      if (!this.detail.dictValue || !this.detail.dictKey) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.dictKeyAndValue'), type: 'warning',})
        return
      }
      this.detail.dictEnable = 1;
      if (!this.form.detailList) {
        this.form.detailList = [];
      }
      this.form.detailList.push(this.detail);
      this.fromif = true;
      this.detail = {};
    },
    // 列表删除
    deleteRow(index, row) {
      // 删除前端渲染数组的数据
      this.form.detailList.splice(index, 1);
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.el-button--text {
  margin-right: 15px;
}

.el-select {
  width: 300px;
}

.el-input {
  width: 300px;
}

.dialog-footer button:first-child {
  margin-right: 10px;
}

.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
